.header, .header__wrap, .wrap {
  *zoom: 1;
}

.header:before, .header__wrap:before, .wrap:before, .header:after, .header__wrap:after, .wrap:after {
  content: " ";
  display: table;
}

.header:after, .header__wrap:after, .wrap:after {
  clear: both;
}

.header__logo-link span {
  overflow: hidden;
  text-indent: -9000px;
  display: block;
}

.centered {
  text-align: center;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.row {
  position: relative;
  margin-left: -10px;
  margin-right: -10px;
}

@media (min-width: 769px) {
  .row {
    margin-left: -20px;
    margin-right: -20px;
  }
}

.row [class^="col"] {
  float: left;
  margin: 0 auto;
  margin-bottom: 20px;
  min-height: 0.125rem;
  padding: 0 10px;
}

@media (min-width: 769px) {
  .row [class^="col"] {
    padding: 0 20px;
    margin-bottom: 0;
  }
  .row [class^="col"].col-offset-1 {
    margin-left: 8.33333%;
  }
}

.row::after {
  content: "";
  display: table;
  clear: both;
}

.col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12 {
  width: 100%;
}

@media (min-width: 769px) {
  .col-1 {
    width: 8.33333%;
  }
  .col-2 {
    width: 16.66667%;
  }
  .col-3 {
    width: 25%;
  }
  .col-4, .col-one-third {
    width: 33.33333%;
  }
  .col-5 {
    width: 41.66667%;
  }
  .col-6, .col-half {
    width: 50%;
  }
  .col-7 {
    width: 58.33333%;
  }
  .col-8, .col-two-thirds {
    width: 66.66667%;
  }
  .col-9 {
    width: 75%;
  }
  .col-10 {
    width: 83.33333%;
  }
  .col-11 {
    width: 91.66667%;
  }
  .col-12 {
    width: 100%;
  }
}

/* =WordPress Core
-------------------------------------------------------------- */
.alignnone {
  margin: 5px 20px 20px 0;
}

.aligncenter,
div.aligncenter {
  display: block;
  margin: 5px auto 5px auto;
}

.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}

.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}

a img.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}

a img.alignnone {
  margin: 5px 20px 20px 0;
}

a img.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}

a img.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.wp-caption {
  background: #fff;
  border: 1px solid #f0f0f0;
  max-width: 96%;
  /* Image does not overflow the content area */
  padding: 5px 3px 10px;
  text-align: center;
}

.wp-caption.alignnone {
  margin: 5px 20px 20px 0;
}

.wp-caption.alignleft {
  margin: 5px 20px 20px 0;
}

.wp-caption.alignright {
  margin: 5px 0 20px 20px;
}

.wp-caption img {
  border: 0 none;
  height: auto;
  margin: 0;
  max-width: 98.5%;
  padding: 0;
  width: auto;
}

.wp-caption p.wp-caption-text {
  font-size: 11px;
  line-height: 17px;
  margin: 0;
  padding: 0 4px 5px;
}

/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  white-space: nowrap;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: #21759b;
  display: block;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}

.hero {
  height: 250px;
  width: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

@media (min-width: 769px) {
  .hero {
    height: 120px;
  }
}

.hamburger-menu {
  position: absolute;
  top: 15.5px;
  right: 20px;
  margin: auto;
  width: 21px;
  height: 19px;
  cursor: pointer;
  z-index: 100;
}

@media (min-width: 769px) {
  .hamburger-menu {
    display: none;
  }
}

.bar, .bar:after, .bar:before {
  width: 21px;
  height: 3px;
}

.bar {
  position: relative;
  transform: translateY(8px);
  background: white;
  transition: all 0ms 100ms;
}

.bar.animate {
  background: transparent;
}

.bar:before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 8px;
  background: white;
  transition: bottom 100ms 100ms cubic-bezier(0.23, 1, 0.32, 1), transform 100ms cubic-bezier(0.23, 1, 0.32, 1);
}

.menu-active .bar:before {
  background: white !important;
}

.bar:after {
  content: "";
  position: absolute;
  left: 0;
  top: 8px;
  background: white;
  transition: top 100ms 100ms cubic-bezier(0.23, 1, 0.32, 1), transform 100ms cubic-bezier(0.23, 1, 0.32, 1);
}

.menu-active .bar:after {
  background: white !important;
}

.bar.animate:after {
  top: 0;
  transform: rotate(45deg);
  transition: top 100ms cubic-bezier(0.23, 1, 0.32, 1), transform 100ms 100ms cubic-bezier(0.23, 1, 0.32, 1);
  background: white;
}

.bar.animate:before {
  bottom: 0;
  transform: rotate(-45deg);
  transition: bottom 100ms cubic-bezier(0.23, 1, 0.32, 1), transform 100ms 100ms cubic-bezier(0.23, 1, 0.32, 1);
  background: white;
}

.header {
  position: relative;
  height: 50px;
  background-color: #00335c;
  width: 100%;
  top: 0;
  left: 0;
  -webkit-backface-visibility: hidden;
  transform: translate3d(0, 0, 0);
  z-index: 100;
  transition: 200ms;
}

.header__wrap {
  margin: 0 auto;
  max-width: 990px;
}

.menu-active .header {
  background-color: #2e4a6b;
  position: absolute;
}

@media (min-width: 769px) {
  .header {
    overflow: visible;
    height: 180px;
    margin: 0 auto;
    padding-top: 40px;
  }
}

.header__logo-link {
  float: left;
  margin-top: 15px;
  margin-left: 15px;
  width: 142px;
  height: 20px;
  position: relative;
  z-index: 100;
}

.header__logo-link span {
  transition: 200ms;
  background-image: url(../img/logo.svg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  height: 20px;
}

.nav {
  padding: 15px;
  width: 100%;
  position: absolute;
  transition: 0s;
  transform: translate3d(0, -100%, 0);
  background-color: rgba(23, 54, 91, 0.9);
}

.menu-active .nav {
  transform: translate3d(0, 50px, 0);
}

.nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 23px;
}

.nav__list a {
  color: white;
  width: 100%;
  padding: 10px 0;
  text-decoration: none;
  text-transform: uppercase;
  display: block;
}

.nav .lang-item {
  display: inline-block;
  margin-right: 15px;
  font-size: 17px;
}

.nav__item {
  display: block;
}

.footer {
  padding: 15px 0;
  background-color: #00335c;
  color: #5dd0e3;
  font-size: 12px;
  line-height: 13px;
}

.footer__col-1 {
  display: none;
}

@media (min-width: 769px) {
  .footer__col-1 {
    display: block;
  }
}

@media (min-width: 769px) {
  .footer__right {
    text-align: right;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  font-family: "Assistant", sans-serif;
  color: #3d3d3b;
  font-size: 15px;
  font-weight: 300;
  line-height: 18px;
}

@media (min-width: 769px) {
  html {
    font-size: 16px;
  }
}

body {
  margin: 0;
  padding: 0;
}

body.menu-active {
  overflow: hidden;
  padding-top: 50px;
}

ul {
  margin: 0;
}

.wrap {
  max-width: 990px;
  margin: 0 auto;
  padding: 0 20px;
}

.wrap--sm {
  max-width: 600px;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 300;
}

h1 {
  font-size: 30px;
  color: #3d3d3b;
  font-weight: 300;
  margin-top: 0;
}

.main {
  padding: 15px 0;
}

.main li {
  list-style: none;
}

.main li:before {
  display: inline-block;
  width: 20px;
  height: 5px;
  background: #8acfe1;
  content: "";
  margin-left: -41px;
  margin-right: 18px;
}
